@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

.bgGradient {
  background: #f2f2f2;
  background-repeat: no-repeat;
  background-image: linear-gradient(to bottom, #051322, #15457c);
  background-size: 100% 603px;
  min-height: 100vh;
}

@media only screen and (max-width: 992px) {
  .bgGradient {
    background-size: 100% 923px;
  }
  .header__container {
    text-align: center;
  }

  .searchSection__inner {
    flex-direction: column;
  }

  .input__box {
    width: 100% !important;
  }

  .search__btn {
    margin-bottom: 0 !important;
  }

  .card__resp {
    flex-direction: column;
    text-align: center;
  }

  .flight__name {
    margin-right: 0 !important;
  }

  .timing__section {
    padding: 5px 8px 5px 8px !important;
  }

  .flight__timings {
    width: 100% !important;
  }

  .timing__options {
    padding: 5px 8px 5px 8px !important;
  }
}

@media only screen and (max-width: 576px) {
  .fill__line {
    max-width: 70px;
  }
}

.make__flex {
  display: flex;
}

.pageHeader {
  position: relative;
  margin-bottom: 40px !important;
}

.header__container {
  width: 1200px;
  margin: 0 auto;
}

.header__logo > img {
  width: 95px;
  padding: 15px 0;
}

.minContainer {
  width: 100%;
  overflow: hidden;
  min-height: 300px;
}

.widget__section {
  width: 80%;
  margin: 0 auto;
  position: relative;
  z-index: 10;
  border-radius: 8px;
  box-shadow: 0 1px 5px 0 rgb(0 0 0 / 10%);
  background-color: #fff;
  padding: 50px 20px 0;
}

.fair__tabs {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  margin: 0 10px;
  padding: 2px 6px;
  font-weight: 900;
}

.fair__tabs > input {
  margin-right: 5px;
}

.searchSection {
  padding: 11px 0;
}

.searchSection__inner {
  width: 100%;
  margin: 0 auto 20px;
  border-radius: 10px;
  align-items: center;
  border: solid 1px #e7e7e7;
  position: relative;
  z-index: 5 !important;
  overflow: hidden;
}

.input__box {
  background: rgba(255, 255, 255, 0.1);
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

.input__box > label {
  padding: 10px 19px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.search__city {
  width: 300px;
  height: 95px;
  border-right: solid 1px #e7e7e7;
}

.lbl__input {
  text-transform: uppercase;
  margin-bottom: 5px;
  font-weight: 700;
}

.input__field {
  border: 0;
  outline: 0;
  background: none;
  min-width: 140px;
  max-height: 36px;
  cursor: pointer;
  padding: 0px;
  margin: 0px;
  line-height: 36px;
  font-weight: 900;
  font-size: 30px;
}

.input__field > option {
  font-weight: 900;
  font-size: 20px;
}

.search__dates {
  width: 166px;
  height: 95px;
  border-right: solid 1px #e7e7e7;
}

.flight__travellers {
  width: 214px;
  border-radius: 0 10px 10px 0;
}

.input__field_number {
  color: #000;
}

.verticle__center {
  justify-content: center;
}

.search__btn {
  width: 216px;
  padding: 10px;
  margin-bottom: -32px;
  opacity: 1;
  border-radius: 34px;
  background-image: linear-gradient(96deg, #53b2fe, #065af3);
  box-shadow: 0 1px 7px 0 rgb(0 0 0 / 20%);
  display: inline-block;
  flex-shrink: 0;
  text-transform: uppercase;
  cursor: pointer;
  outline: 0;
  border: 0;
  text-align: center;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  color: #fff;
}

.make__flex {
  display: flex;
}

.space__between {
  justify-content: space-between;
}

.slideContainer {
  width: 100%;
  margin: 20px auto;
  text-align: center;
  display: block;
}

.slideContainer>p{
  color: #fff;
}

.slider {
  width: 80%;
  height: 15px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.15s ease-in-out;
}

.slider:hover {
  opacity: 1;
}

.flightBody {
  width: 100%;
  overflow: hidden;
  position: relative;
  padding-top: 20px;
}

.flights__container {
  width: 80%;
  margin: 0 auto;
  position: relative;
}

.listingcard__wrap {
  box-shadow: 0 3px 30px 0 rgb(0 0 0 / 10%);
  border-radius: 2px;
  background: #fff;
  margin-bottom: 35px;
  overflow: hidden;
}

.listing__card {
  padding: 16px 20px;
  border-bottom: 1px solid #eaeaea;
  background: #fff;
  border-radius: 2px;
  position: relative;
}

.flight__name {
  margin-right: 15px;
  align-items: center;
  justify-content: center;
}

.flight__name > img {
  width: 32px;
  height: 32px;
}

.flight__name > span {
  margin-left: 10px;
  width: 100px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-weight: 700;
}

.timing__section {
  width: 100%;
  border: 1px solid transparent;
  padding: 5px 8px 5px 42px;
  position: relative;
  overflow: hidden;
}

.flight__timings {
  margin: 0;
  width: 582px;
}

.timing__options {
  width: 100%;
  border: 1px solid transparent;
  padding: 5px 8px 5px 42px;
  position: relative;
  flex-direction: column;
}

.departure__timing {
  font-weight: 900;
  font-size: 18px;
}

.departure__city {
  color: #4a4a4a;
}

.fill__section {
  width: 100%;
  position: relative;
}

.fill__line {
  border-top: 3px solid rgb(245, 166, 34);
  width: 70px;
  height: 2px;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 2px;
  bottom: 0;
  z-index: 1;
}

